import WXPlatform from "./WXPlatform";
import { EPlatformType } from "./EPlatformType";
import LTPlatformData from "./Data/LTPlatformData";
import { CommonEventId } from "../Commom/CommonEventId";

export default class QTTPlatform extends WXPlatform {

    platform: EPlatformType = EPlatformType.QTT;


    Init(platformData: LTPlatformData) {
        this._base = window["qttGame"];
        if (this._base == null) {
            console.error("");
            return;
        }

        this._platformData = platformData;
        // this._InitLauchOption();
        // this._Login();
        // this._InitShareInfo();
        // this._InitSystemInfo();
        // this._CreateBannerAd();
        // this._CreateVideoAd();
        // this._CreateInterstitalAd();

        window["iplatform"] = this;
    }

    IsBannerAvaliable() {
        return true;
    }

    ShowBannerAd() {
        this._base.showBanner({ index: 1 });
    }

    HideBannerAd() {
        this._base.hideBanner();
    }

    IsVideoAvaliable() {
        return true;
    }

    ShowRewardVideoAd(onSuccess: Laya.Handler, onSkipped: Laya.Handler) {
        let options = {} as any;
        options.index = 1;//1234...
        options.gametype = 1;//1()  2(laba)  3()
        options.rewardtype = 1;// 1
        options.data = {};
        options.data.title = "";//
        Laya.stage.event(CommonEventId.PAUSE_AUDIO);
        this._base.showVideo(
            (res) => {
                Laya.stage.event(CommonEventId.RESUM_AUDIO);
                if (res == 1) {
                    // 
                    if (onSuccess) {
                        onSuccess.run();
                    }
                } else {
                    //res = 0    
                    //res = 2    
                    if (onSkipped) {
                        onSkipped.run();
                    }
                }
            }
            , options);
    }

    ShowInterstitalAd() {
        // 
        this.ShowHDReward();
    }

    /**
     * 
     */
    public ShowHDReward() {
        let options = {} as any;
        options.index = 1;
        options.rewardtype = 1;
        this._base.showHDReward(options);
    }

    RecordEvent(eventId: string, param: object) {
        console.log("", eventId, param);
    }
}